<template>
   <v-container class="">     
     <Horario_S :datos="datos" />      
    </v-container>
</template>
<script>
import axios from "axios";
import { mapMutations , mapState} from "vuex";
import Horario_S from '../components/Horario_semanal.vue'
export default {
 components: {
      Horario_S,
    },
  mounted() {
    this.Cargar_Datos()
  },
  data() {
    return {
      datos:[]
    }
  },
  computed: {
     ...mapState(['ip']),
  },
  methods: {
    ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),

    async Cargar_Datos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Horario");
      this.Modificar_Espera(true);
      this.loading = true;
      const json = JSON.stringify({
        token: localStorage.getItem("Token"),        
      });

      try {
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/Horario",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        if (!this.error && !res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
          this.Modificar_Informacion("Correcta");
          
          res.data.Datos.forEach(element => {
            this.datos.push({
              dia:element.Dia,
              entrada:element.Entrada,
              salida:element.Salida
            })
          });             

          this.Modificar_Boton(true);
          this.loading = false;
        } else if (res.data.Error) {
            
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);          
          this.Modificar_Datos("Sin Registros");          
          this.Modificar_Boton(true);
          this.loading = false;
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);
        console.log(error);        
        this.loading = false;
      }
    },
  },
}
</script>